import {
  Builder,
} from '@builder.io/sdk';

import {requirejs, define} from '@steedos-builder/requirejs';
// const requirejs = require("requirejs")

export class SteedosBuilder extends Builder {

  static settings = {
    unpkgUrl: "https://npm.elemecdn.com"
  }

  // importMap for requirejs
  static importMap = {}

  static stores = {}

  static requirejs = requirejs

  static define = define

  static require(params: any, callback?: Function) : any {
    if (!Builder.isBrowser) {
      console.warn('Builder.require used on the server - this should only be used in the browser');
      return;
    }
    return SteedosBuilder.requirejs(params, callback);
  }

  // Register remote to requirejs, so you can do require('@steedos-builder/sdk') in browser.
  // params: {'@steedos-builder/sdk': 'https://unpkg.com/@steedos-builder/sdk/dist/index.umd.js'}
  static registerImportMap( importMap:any ) : any {
    if (Builder.isBrowser) {

      Object.assign(this.importMap, importMap);
      const message = {
        type: 'builder.importMapChange',
        data: this.importMap,
      };
      parent.postMessage(message, '*');

      return SteedosBuilder.requirejs.config({
        paths: this.importMap
      });
    }
  }

  static registerRemoteAssets = async (assetUrl: string, callback: Function) => {
  
    if (!Builder.isBrowser) {
      console.warn('Builder.require used on the server - this should only be used in the browser');
      return;
    }
    
    var url = assetUrl.replace("https://unpkg.com", SteedosBuilder.settings.unpkgUrl);
    Builder.register('remote-assets', {url: url})

    SteedosBuilder.requirejs([url], (assets: any) => {
      SteedosBuilder.registerAssets(assets, callback);
    });
  };
  
  static registerAssets = async ( assets: any, callback: Function) => {

    const {packages, components} = assets;

    if (!Builder.isBrowser) {
      console.warn('Builder.require used on the server - this should only be used in the browser');
      return;
    }

    Builder.register('assets', {assets})

    const packageNames: any = [];
    const packageExports: any = [];
    const paths:any = {};
  
    packages?.forEach(async (pkg: any, index: number) => {
      // console.log(pkg, index);
      if (pkg.urls && Array.isArray(pkg.urls) && pkg.urls.length > 0) {
        if (pkg.package && pkg.library) {
          var url: string = pkg.urls[0];
          var cssUrl: string = pkg.urls[1];
          if (url?.endsWith(".js")) {
            url = url.replace("https://unpkg.com", SteedosBuilder.settings.unpkgUrl);
            url = url.substring(0, url.length - 3);
            paths[pkg.package] = url;
            packageNames.push(pkg.package);
            packageExports.push(pkg.library);
          }
          if (cssUrl?.endsWith(".css")) {
            cssUrl = cssUrl.replace("https://unpkg.com", SteedosBuilder.settings.unpkgUrl);
            SteedosBuilder.injectCSS(cssUrl)
          }
        }
      }
    });
    SteedosBuilder.registerImportMap(paths);

    SteedosBuilder.requirejs(packageNames, () => {
      packageExports.forEach( (exportName: string, index: any) => {
        console.log(`importing ${exportName}`)
        if ((window as any)[exportName] === "undefined") {
          (window as any)[exportName] = SteedosBuilder.requirejs(packageNames[index]);
        }
      });

      components?.forEach(async (comp: any, index: number) => {
        if (comp.url) {
          const compUrl = comp.url.replace("https://unpkg.com", SteedosBuilder.settings.unpkgUrl);
          SteedosBuilder.requirejs([compUrl], (meta: any) => {
            if (comp.exportName && typeof window[comp.exportName] === "undefined") {
              window[comp.exportName] = meta;
            }
            SteedosBuilder.registerMeta(meta);
          });
        }
      });

      if (callback)
        callback();
    });
  };
  
  
  static registerMeta = (meta: any) => {

    Builder.register('metas', {meta})

    if (meta.components) {
      meta.components.forEach(async (comp: any, index: number) => {
        if (comp.npm?.package && comp.npm?.exportName) {
          const pkg = SteedosBuilder.requirejs(comp.npm.package);
          Builder.registerComponent(
            pkg[comp.npm.exportName],
            { name: comp.componentName }
          );
        }
        Builder.register("meta-components", comp);
        // register component to engine
        // ...
      });
    }
  };

  static registerAmis = ({render, editor}:any) => {
    if (render)
      Builder.registerComponent('AmisRender', render)
    if (editor)
      Builder.registerComponent('AmisEditor', editor)
  }
  
  static injectCSS(cssUrl:string, { insertAt = 'top' } = {}) {
    if (!cssUrl || typeof document === 'undefined') return
  
    const head = document.head || document.getElementsByTagName('head')[0]
    const link = document.createElement('link')
    link.type = 'text/css'
    link.rel = "stylesheet"
    link.href = cssUrl;

    if (insertAt === 'top') {
      if (head.firstChild) {
        head.insertBefore(link, head.firstChild)
      } else {
        head.appendChild(link)
      }
    } else {
      head.appendChild(link)
    }
  }

  get host() {
    switch (this.env) {
      case 'development':
      case 'dev':
        return 'http://localhost:5000';
      default:
        return Builder.overrideHost || 'https://console.steedos.cn';
    }
  }

}


if (Builder.isBrowser) {
  window.define = define;
  window.requirejs = requirejs;
}